<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="false" :showCheckBox="true" :listType="[DetailListType]" ModuleName="Asset" SubModuleCode="Timer" IdentityColumn="group_id" HeaderText="Group List" :ListData="GroupData" :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                <td class="text-center single-action">
                    <div class="cstm-drop-btn">
                        <select v-model="data.row.STATUS_ID" @change="DrpdownUpdateStatus(data.row.STATUS_ID,data.row.group_id);" :class="data.row.STATUS_ID==1001?'ddlupdatestatus w-80 btn-success' :'ddlupdatestatus w-80 btn-danger'">
                            <option value="1001" selected="selected">Active</option>
                            <option value="1002">Inactive</option>
                        </select>
                    </div>
                </td>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'TotalItemsCount'">
                <span class="text-dark" :title="data.row.TotalItemsCount">
                    <i class="fa fa-briefcase" aria-hidden="true"></i>
                    <span class="noti-circle noti-blue">{{ data.row.TotalItemsCount}}</span>
                </span>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'group_name'">
                <span class="text-dark" v-if="data.row.group_name">
                    <a href="javascript:;" v-on:click="EditGroup(data.row.group_id)" :id="data.row.group_id" :title="data.row.group_name"><em class="text-primary" aria-hidden="true">{{ data.row.group_name }}</em></a>
                </span>

            </template>
        </template>
    </tg-list>
</section>
</template>

<script>
import DataService from '../../service/DataService';
import Schema from '../../views/ItemGroupList/ListSchema'
export default {

    data() {
        return {
            CategorytList: [],
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: [

                {
                    "COLUMN_NAME": 'group_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('GroupName'),
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true,
                    }

                },
                {
                    "COLUMN_NAME": 'group_details',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Detail'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": 'device_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('DeviceName'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'item_description',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('AssignedReader'),
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": 'TotalItemsCount',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('TotalItems'),
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": 'STATUS_ID',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Status'),
                    "DISPLAY_ORDER": 6,
                    settings: {
                        isInSlot: true
                    }
                }

            ],
            noRecord: true,
            statusIds: '',
            LocationId: '',
            SortBy: '',
            SortExp: '',
            IsDefault: '',
            GroupData: [],
            noRecord: false,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            isLoading: false,
            StatusData: [],
            listheaderbuttons: Schema.buttons,
            isLoading: false,
            DetailListType: 'List',
        }
    },
    created() {
        this.FetchData();
    },
    methods: {
        async FetchData() {
            var vueObj = this;
            var url = `${this.searchCondition}&statusIds=${this.statusIds}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&IsDefault=${this.IsDefault}`;
            DataService.GetItemGroupList(url).then(response => {
                if (response.data != null) {
                    if (response.data.data.length > 0) {
                        vueObj.GroupData = response.data.data;
                        vueObj.TotalRecords = response.data.data[0].TOTALRECORDS
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                    } else {
                        vueObj.GroupData = [];
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.GroupData = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function () {
                    vueObj.CheckBoxBootstrap()
                }, 100);
            });
            vm.isLoading = false;
        },
        DrpdownUpdateStatus(statusId, groupid) {
            var vm = this;
            vm.isLoading = true;
            const Object = {
                ID: groupid,
                StatusId: statusId,
                IsFor: 'TALYGEN_IOT_ITEMS_GROUP'
            }
            var PostString = JSON.stringify(Object);
            vm.confirmR(vm.$t('Are you sure to update this record?'), true, false, function (result) {
                DataService.CommonUpdateStatus(PostString).then(response => {
                    var status = ''
                    if (statusId == '1001') {
                        status = 'Active';
                        vm.isLoading = false;
                    } else {
                        vm.isLoading = false;
                        status = 'Inactive';
                    }
                    vm.ShowAlert(status + vm.$t(' Updated Successfully'), "success", ('Alert'));
                    vm.FetchData();
                    vm.isLoading = false;
                })
            })
            vm.FetchData();
            vm.isLoading = false;
        },
        Delete() {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group_Item")), function () {
                    var postJSON = {
                        ids: val,
                        tblname: "Talygen_IoT_Items_Group",
                        primarycolname: "group_id"
                    };
                    vm.isLoading = true;
                    var poststing = JSON.stringify(postJSON);
                    DataService.DeleteGroupList(poststing).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Group_Item')]), "Success", true, vm.$t('Alert'));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Item')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.isLoading = false;
                    });
                });
            }
        },
        actionButtonClick(callbackfunction, item) {},
        renderActions(row) {},
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "DeleteData":
                    this.Delete()
                    break;
                case "AddNewGroup":
                    this.AddNewGroup()
                    break;
            }
        },
        AddNewGroup: function () {
            this.$router.push({
                name: 'ManageGroup',
                params: {
                    id: 0
                }
            });
        },
        EditGroup: function (Groupid) {
            this.$router.push({
                name: 'ManageGroup',
                params: {
                    id: Groupid
                }
            });
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
    }
}
</script>
